:root {
    --primary: #5b68f0;
}

@font-face {
    font-family: 'stolzlbold';
    src: url(/WinterIO/static/media/bold-webfont.8d8956d1.woff2) format('woff2'),
         url(/WinterIO/static/media/bold-webfont.c498c8f6.woff) format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  background-color: #afe6e6;
  margin: 0;
  padding: 0;
  font-family: 'Archivo', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1 {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}
.Form {
    order: 1;
    width: 80%;
    background-color: white;
    padding: .6em;
    border-radius: 8px;
    box-sizing: border-box;
    -webkit-transition-delay: .85s;
            transition-delay: .85s;
    -webkit-transition: 1s ease;
    transition: 1s ease;
}

.Form__input {
    width: 40%;
    margin: 5%;
    padding: 1em 1em 0.5em 0em;
    box-sizing: border-box;
    margin-bottom: 1em;
    color: var(--primary);
    font-size: 4vw;
    border: 0;
    border-bottom: 1px solid lightgray;
    -webkit-transition: .35s ease;
    transition: .35s ease;
}

.Form__input:focus {
    outline: none;
    border-bottom: 1px solid var(--primary);
}

.Form__submit {
    border: 0;
    border-radius: 8px;
    background-color: var(--primary);
    color: white;
    cursor: pointer;
    display: block;
    font-size: 3.3vw;
    font-weight: bold;
    font-family: inherit;
    letter-spacing: 1px;
    margin-top: 1em;
    margin-bottom: -2.5em;
    outline: none;
    padding: 1.2em 2em;
    -webkit-transition: .25s ease;
    transition: .25s ease;
    text-transform: uppercase;
    width: 100%;
}

.Form__submit:hover {
    background-color: black;
}
.Weather {
    opacity: 0;
    order: 2;
    height: 0vh;
    margin-left: 2em;
    border-radius: 8px;
    box-sizing: border-box;
    width: 80%;
    margin: 3em auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    -webkit-transition: .75s ease;
    transition: .75s ease;
}

.weather_icon {
    width: 20%;
    margin-top: 2em;
}

.Weather.found {
    opacity: 1;
    height: 50vh;
}

.Weather__info {
    width: 95%;
    padding: 2em;
    position: relative;
}

.weather__location h1, 
.weather__description {
    font-size: 5vw;
}

.weather__description {
    margin-top: .8em;
}

.weather__location {
    text-transform: uppercase;
    letter-spacing: 3px;
    margin-bottom: .5em;
}

.weather__temperature {
    font-size: 16vw;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: .6em auto .4em;
    letter-spacing: 2px;
}

.weather__ranges {
    font-size: 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.weather__maxtemp,
.weather__mintemp {
    flex-basis: 50%;
}

.weather__maxtemp::after,
.weather__mintemp::after {
    content: "\BA";
}

.weather__description {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: bold;
}

.floating {
    -webkit-animation: float 6s ease-in-out infinite;
            animation: float 6s ease-in-out infinite;
}

@-webkit-keyframes float {
	0% { -webkit-transform: translatey(0px); transform: translatey(0px) }
	50% { -webkit-transform: translatey(-20px); transform: translatey(-20px) }
	100% { -webkit-transform: translatey(0px); transform: translatey(0px) }
}

@keyframes float {
	0% { -webkit-transform: translatey(0px); transform: translatey(0px) }
	50% { -webkit-transform: translatey(-20px); transform: translatey(-20px) }
	100% { -webkit-transform: translatey(0px); transform: translatey(0px) }
}
.Error {
    width: 95%;
    border-radius: 8px;
    margin: 1em auto;
    display: flex;
    position: absolute;
    padding: 2em 1em;
    box-sizing: border-box;
    bottom: 10%;
    background-color: #ef4646;
    color: white;
    text-align: center;
    opacity: 1;
    -webkit-transition: .85s ease;
    transition: .85s ease;
}

.Error.hidden {
    opacity: 0;
}
.Navbar {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: 0 auto 1em;
    height: 60px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.Navbar__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    background-color: #f5f5f5;
    text-align: center;
    font-size: 3.2vw;
    letter-spacing: .5px;
    width: 30%;
    flex-basis: 30%;
    height: 7vh;
    text-transform: uppercase;
    cursor: pointer;
    border: 0px;
    -webkit-transition: .25s ease;
    transition: .25s ease;
}

.Navbar__tab:focus {
    outline: none;
}

.Navbar__tab.active {
    background-color: var(--primary);
    box-shadow: unset;
    color: white;
    -webkit-transform: translateY(-8%);
            transform: translateY(-8%);
}
.App {
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: -webkit-linear-gradient(330deg, #84fab0 0%, #8fd3f4 100%);
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}
